/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #221D23;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** LAYOUT **/
/** Media Queries **/
.header {
  background-color: whitesmoke;
  padding: 1rem 0 3rem 0;
}

a {
  color: #4F3824;
  font-weight: 700;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-banner {
  background-color: #221D23;
  color: antiquewhite;
  text-transform: uppercase;
}

/** Media Queries **/
footer {
  background-color: #D1603D;
}
footer .contenido-footer {
  padding-top: 2.5rem;
}

.copyright {
  color: antiquewhite;
  text-align: center;
}

p {
  color: antiquewhite;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #221D23;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.6rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.navegacion a:hover {
  transition: ease-in-out 0.5s;
  color: #67266a; /* Color de fondo al pasar el mouse */
}

/** Media Queries **/
.sidebar {
  height: 80vh; /* Altura completa de la ventana de visualización */
  width: 250px; /* Ancho de la barra lateral */
  position: relative; /* Posicionamiento fijo */
  left: 0rem; /* A la izquierda de la página */
  top: 0; /* En la parte superior de la página */
  background-color: #DDB967; /* Color de fondo */
  padding: 20px; /* Espaciado interno */
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5); /* Sombra para efecto 3D */
}

.sidebar a {
  display: block; /* Los enlaces ocupan toda la línea */
  color: #221D23; /* Color del texto */
  padding: 30px; /* Espaciado interno para los enlaces */
  text-decoration: none; /* No subrayado */
  font-size: 18px; /* Tamaño del texto */
}

.sidebar a:hover {
  transition: ease-in-out 0.5s;
  color: darkgoldenrod;
}

/* Ajustar el contenido principal para no estar detrás de la barra lateral */
.contenedor {
  margin-left: 250px; /* Mismo ancho que la barra lateral */
}

/** PAGINAS INTERNAS **/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: wheat;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #f76a6a;
}

/** Media Queries **/
body {
  background-color: antiquewhite;
}

.banner-form {
  font-family: "Overlock", sans-serif;
  color: #D0E37F;
}

.banner {
  background-color: #221D23;
}